import React, { useState, useCallback } from 'react';
import { Formik } from 'formik';
import { object, string } from 'yup';

import Input from './Input';
import TextArea from './TextArea';
import Error from './Error';
import Button from './Button';

import { fonts } from '../constants';
import { identifySegment, trackSegment } from '../utils/segment';
import { getCampaign } from '../utils/utm';

export default function BlockContactSalesForm() {
  const [error, setError] = useState(null);
  const [showSuccess, setShowSuccess] = useState(false);

  const onSubmit = useCallback(async (values, { setSubmitting, resetForm }) => {
    try {
      setError(null);
      const { name, email, phone, company, website, message } = values;
      values.campaign = getCampaign();

      identifySegment({
        email,
        phone,
        name,
        company: { name: company },
        website,
      });
      trackSegment('Contact form submitted', {
        name,
        email,
        phone,
        company,
        website,
        message,
        context: {
          campaign: getCampaign(),
        },
      });

      const host =
        window.location.hostname === 'localhost'
          ? 'http://localhost:2222'
          : 'https://app.resolvepay.com';

      const res = await fetch(`${host}/api/web/contact`, {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(values),
      }).then(body => body.json());

      if (res && res.success) {
        resetForm();
        setShowSuccess(true);
      }
    } catch (err) {
      setError(err);
    } finally {
      setSubmitting(false);
    }
  }, []);

  return (
    <div>
      {showSuccess ? (
        <div>
          <p className="contact_sales__subtitle">
            Thanks! We'll get back to you soon
          </p>
        </div>
      ) : (
        <div>
          <p className="contact_sales__subtitle">
            Tell us how to reach you and let's discuss how Resolve can grow your
            b2b business.
          </p>
          <div className="contact_sales_form__form-container">
            <Formik
              initialValues={{
                name: '',
                email: '',
                phone: '',
                company: '',
                website: '',
                message: '',
              }}
              validationSchema={object().shape({
                name: string().trim().required('Required'),
                email: string()
                  .trim()
                  .lowercase()
                  .email('Invalid email')
                  .required('Required'),
                company: string().trim().required('Required'),
                website: string().trim().lowercase(),
                message: string().trim().required('Required'),
              })}
              onSubmit={onSubmit}
            >
              {({
                values,
                errors,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
              }) => (
                <form onSubmit={handleSubmit}>
                  <Input
                    id="name"
                    name="name"
                    placeholder="Name"
                    error={errors.name}
                    value={values.name}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <Input
                    id="email"
                    name="email"
                    placeholder="Work email"
                    error={errors.email}
                    value={values.email}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <Input
                    id="phone"
                    name="phone"
                    type="tel"
                    placeholder="Phone number"
                    error={errors.phone}
                    value={values.phone}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <Input
                    id="company"
                    name="company"
                    type="text"
                    placeholder="Company name"
                    error={errors.company}
                    value={values.company}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <Input
                    id="website"
                    name="website"
                    type="text"
                    placeholder="Company website"
                    error={errors.website}
                    value={values.website}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <TextArea
                    id="message"
                    placeholder="Type your message..."
                    error={errors.message}
                    value={values.message}
                    onChange={handleChange}
                    onBlur={handleBlur}
                  />
                  <Error
                    error={
                      error &&
                      'There was an error submitting your request. Please try again.'
                    }
                  />
                  <Button
                    type="submit"
                    primary
                    disabled={isSubmitting}
                    className="contact_sales_form__submit"
                  >
                    Submit
                  </Button>
                </form>
              )}
            </Formik>
          </div>
        </div>
      )}
      <style jsx>{`
        .contact_sales__subtitle {
          font-family: ${fonts.halyardDisplay};
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
          margin: 20px 0 40px;
          text-align: center;
        }
      `}</style>
      <style jsx global>{`
        .contact_sales_form__submit {
          width: 100%;
        }
      `}</style>
    </div>
  );
}
